import React, { Component } from 'react'
import CrosswordJSON from '../../assets/MyCrossword'
import './Crossword.css'

export default class Crossword extends Component {
    renderGrid = (grid) => {
        var gridRendering = grid.map(letter => <div className="square">{letter}</div>)
        return gridRendering

    }
    render() {
        console.log(CrosswordJSON.Grid)
        return (
            <div className="board">
                {CrosswordJSON.Grid.map(letter => <div className="square">{letter}</div>)}
            </div>
        )
    }
}
